.btn {
  display: inline-block;
  font-size: 16px;
  font-weight: 300;
  text-align: center;
  line-height: 44px;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  border-radius: 2px;
  padding: 0 31px;
  text-decoration: none;
  transition-duration: .1s;
}

.btn:hover, .btn.is-active {
  outline: 0;
  text-decoration: none;
}

.btn--block {
  display: block;
  width: 100%;
  max-width: 100%;
}

.btn--sm {
  line-height: 31px;
}

.btn--primary {
  background-color: #e61f32;
  border-color: #e61f32;
  color: white;
}

.btn--primary:hover, .btn--primary.is-active {
  background-color: #c61627;
  border-color: #c61627;
  color: white;
}

.btn--primary.btn--outline {
  background-color: transparent;
  color: #e61f32;
}

.btn--primary.btn--outline:hover, .btn--primary.btn--outline.is-active {
  background-color: #e61f32;
  border-color: #e61f32;
  color: white;
}

.btn--secondary {
  background-color: #f7ebc3;
  border-color: #f7ebc3;
  color: #786010;
}

.btn--secondary:hover, .btn--secondary.is-active {
  background-color: #f2df9f;
  border-color: #f2df9f;
  color: #786010;
}

.btn--secondary.btn--outline {
  background-color: transparent;
  color: #f7ebc3;
}

.btn--secondary.btn--outline:hover, .btn--secondary.btn--outline.is-active {
  background-color: #f7ebc3;
  border-color: #f7ebc3;
  color: white;
}

.btn--light {
  background-color: #eceff1;
  border-color: #eceff1;
  color: #455a64;
}

.btn--light:hover, .btn--light.is-active {
  color: #455a64;
  background-color: white;
}

.btn--light.btn--outline {
  background-color: transparent;
  color: #eceff1;
}

.btn--light.btn--outline:hover {
  background-color: white;
  border-color: white;
  color: #455a64;
}

